:root {
  --font-body: "Space Mono", monospace;
}

:root {
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
  --strongcyan: hsl(172, 67%, 45%);
  --verydarkcyan: hsl(183, 100%, 15%);
  --darkgrayishcyan: hsl(184, 14%, 56%);
  --lightgrayishcyan: hsl(189, 41%, 97%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-size: 24px;
  background-color: var(--darkgrayishcyan);
  font-family: var(--font-body);
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.container {
  padding: 0 1.5rem;
  max-width: 90rem;
  margin: 0 auto;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
    transform: translate(0, 0);
  }
}

.navbar {
  display: block;
  margin: 8rem auto 4rem auto;
  padding: 0;
  text-align: center;
  color: var(--verydarkcyan);
  font-size: 0.8rem;
}
.navbar h1 {
  display: block;
  margin: 0 auto;
  width: 100px;
  line-height: normal;
  letter-spacing: 0.5rem;
}

form {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.calc .calc-container {
  display: flex;
  margin: auto;
  width: 60%;
  height: 25rem;
  background-color: var(--white);
}
.calc .calc-container .calc-inputs {
  display: grid;
  width: 50%;
  margin: 2rem;
}
.calc .calc-container .calc-inputs input[type=number] {
  -moz-appearance: textfield;
  padding: 1rem;
  background-color: var(--lightgrayishcyan);
  border: none;
  border-radius: 5px;
  color: var(--verydarkcyan);
  font-size: 1.2rem;
  font-weight: 700;
  text-align: right;
  outline: none;
}
.calc .calc-container .calc-inputs label {
  color: var(--darkgrayishcyan);
  font-size: 0.8rem;
  font-weight: 700;
}
.calc .calc-container .calc-inputs .dollar {
  display: inline-block;
  width: fit-content;
  position: relative;
  top: -44px;
  left: 10px;
  color: var(--darkgrayishcyan);
}
.calc .calc-container .calc-inputs .calc-tip-title {
  color: var(--darkgrayishcyan);
  font-size: 0.8rem;
}
.calc .calc-container .calc-inputs .calc-percs {
  display: grid;
  justify-content: center;
  grid-template-columns: repeat(3, 1fr);
  text-align: center;
  gap: 1rem;
}
.calc .calc-container .calc-inputs .calc-percs .option-6, .calc .calc-container .calc-inputs .calc-percs .option-5, .calc .calc-container .calc-inputs .calc-percs .option-4, .calc .calc-container .calc-inputs .calc-percs .option-3, .calc .calc-container .calc-inputs .calc-percs .option-2, .calc .calc-container .calc-inputs .calc-percs .option-1 {
  background-color: var(--verydarkcyan);
  color: var(--white);
  font-weight: 700;
  border-radius: 5px;
  padding: 0.4rem 0;
}
.calc .calc-container .calc-inputs .calc-percs .option-6 {
  background-color: var(--lightgrayishcyan);
  color: var(--verydarkcyan);
}
.calc .calc-container .calc-inputs label[class=calc-people] {
  margin-top: 2rem;
}
.calc .calc-container .calc-visor {
  display: grid;
  width: 50%;
  margin: 2rem;
  border: 1px solid green;
}